import * as React from 'react';
import Collapsible from '../../collapsible';
import { Input, Button, Tooltip } from '@material-tailwind/react';
import { LuTrash2 } from 'react-icons/lu';
import { AiOutlineInfoCircle } from 'react-icons/ai';
import { FaPlus } from 'react-icons/fa6';
import RichTextEditor from '../../richtexteditor';
import services from '../../../../../../../utils/services';
import { usePathViewContext } from '../../../context/pathViewContext';
import { useEditCollapseContext } from '../../../context/editCollapseContext';
import { useSuccessModalContext } from '../../../context/successModalContext';
import { RecruitmentData } from './home.type';
import InputFile from '../../InputFile';

const initialValue: RecruitmentData = {
  section_1: {
    flow_recruit: [
      {
        flow: {
          title: '',
          body: '',
        },
      },
    ],
    tag_title_1: '',
    head_title_1: '',
    section_image_src_1: '',
  },
  section_2: {
    head_title_3: '',
    step_recruit: [
      {
        step: {
          title: '',
          body: '',
        },
      },
    ],
    head_subtitle_3: '',
  },
  hero_banner: {
    banner: [
      {
        banner_image_src: '',
      },
    ],
    main_title: '',
    main_subtitle: '',
  },
  job_position: {
    head_title_2: '',
    head_subtitle_2: '',
    tag_title_2: '',
  },
};

export default function Home({ tabValue }: { tabValue: string }) {
  const [isLoadingGetData, setIsLoadingGetData] = React.useState<boolean>(true);
  const [homeData, setHomeData] = React.useState<RecruitmentData>(initialValue);
  const { activeTabs } = usePathViewContext();

  const getDataByValueTab = async (valueTab: string) => {
    setIsLoadingGetData(true);
    try {
      const payload = {
        page_name: valueTab,
      };
      const response = await services.post('/setting/company_profile', payload);
      const result = response.data;
      setHomeData((prev) => ({ ...prev, ...result }));
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingGetData(false);
    }
  };

  React.useEffect(() => {
    if (tabValue === activeTabs.value) {
      getDataByValueTab(tabValue);
    }
  }, [tabValue, activeTabs]);

  if (isLoadingGetData) return <div>loading ...</div>;

  return (
    <main className="space-y-2">
      <HeroBanner data={homeData.hero_banner} setData={setHomeData} getDataByValueTab={getDataByValueTab} />
      <SectionOne data={homeData.section_1} setData={setHomeData} getDataByValueTab={getDataByValueTab} />
      <SectionTwo data={homeData.section_2} setData={setHomeData} />
      <JobPosition data={homeData.job_position} setData={setHomeData} />
    </main>
  );
}

function HeroBanner({ data, setData, getDataByValueTab }) {
  const { activeTabs } = usePathViewContext();
  const { setIsOpenModalSuccess } = useSuccessModalContext();

  const [isEdit, setIsEdit] = React.useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const handleAddMore = () => {
    setData((prev) => ({
      ...prev,
      hero_banner: {
        ...prev.hero_banner,
        banner: [...prev.hero_banner.banner, { banner_image_src: '' }],
      },
    }));
  };

  const handleRemoveList = (index: number) => {
    setData((prevState) => ({
      ...prevState,
      hero_banner: {
        ...prevState.hero_banner,
        banner: prevState.hero_banner.banner.filter((_, i) => i !== index),
      },
    }));
  };

  const handleInputChange = (e, index?) => {
    const { name, files, value } = e.target;

    if (files && files.length > 0) {
      setData((prev) => {
        const updatedBanner = [...prev.hero_banner.banner];

        updatedBanner[index].banner_image_src = files[0];
        return {
          ...prev,
          hero_banner: {
            ...prev.hero_banner,
            banner: updatedBanner,
          },
        };
      });
    }
    setData((prev) => ({
      ...prev,
      hero_banner: {
        ...prev.hero_banner,
        [name]: value,
      },
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    setIsLoadingSubmit(true);
    const payload = {
      page_name: activeTabs.value,
      card_group: 'hero_banner', // '{faq/headers_footers/hero_banner/job_position/section_1/section_2/section_3}'
      data: data,
    };
    try {
      const response = await services.post('/setting/company_profile/save', payload);
      if (response.statusCode === 200) {
        getDataByValueTab();
        setIsEdit(false);
        setIsOpenModalSuccess(true);
      }
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingSubmit(false);
    }
  };

  const handleDeleteFile = (index) => {
    setData((prev) => {
      const updatedBanner = [...prev.hero_banner.banner];

      updatedBanner[index].banner_image_src = '';

      return {
        ...prev,
        hero_banner: {
          ...prev.hero_banner,
          banner: updatedBanner,
        },
      };
    });
  };

  return (
    <form onSubmit={handleSubmit}>
      <Collapsible isLoading={isLoadingSubmit} isEdit={isEdit} setIsEdit={setIsEdit} footer title="Hero Banner">
        {isEdit ? (
          <div className="p-3 space-y-5">
            <div className="space-y-3">
              <TagLine title="Hero Banner" />
              {data.banner.map((item, index) => (
                <div key={index}>
                  {index !== 0 && (
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-1 mb-1.5">
                        Upload Image/video
                        <Tooltip content="img ratio: 150 x 37.66">
                          <Button type="button" variant="text" size="lg" className="p-0">
                            <AiOutlineInfoCircle className="text-gray-500" />
                          </Button>
                        </Tooltip>
                        <span className="text-danger-500 text-xs">*Format jpg/png/jpeg. max 1MB</span>
                      </div>
                      <button
                        onClick={() => handleRemoveList(index)}
                        type="button"
                        className="p-0.5 rounded-md hover:bg-gray-200 text-slate-600 disabled:bg-gray-100 disabled:cursor-not-allowed disabled:text-gray-400"
                      >
                        <LuTrash2 className="size-4" />
                      </button>
                    </div>
                  )}
                  <InputFile
                    index={index}
                    onRemove={() => handleDeleteFile(index)}
                    value={item.banner_image_src}
                    id={`banner_${index}`}
                    name="banner_image_src"
                    type="file"
                  />
                </div>
              ))}
              <div className="flex justify-end">
                <Button
                  type="button"
                  onClick={handleAddMore}
                  variant="outlined"
                  size="sm"
                  className="normal-case text-xs text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center gap-x-1"
                >
                  Add More
                  <FaPlus className="size-3 text-blue-600" />
                </Button>
              </div>
            </div>

            <div className="space-y-3">
              <TagLine title="Company Tagline" />
              {/* Title */}
              <div>
                <label htmlFor="company_tagline_1" className="w-full">
                  Company Tagline 1
                </label>
                {/* <p className="">Title</p> */}
                <input
                  onChange={handleInputChange}
                  value={data.main_title}
                  name="main_title"
                  id="company_tagline_1"
                  type="text"
                  placeholder="Company Tagline 1"
                  className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                />
              </div>

              {/* Subtitle */}
              <div>
                <label htmlFor="company_tagline_2" className="w-full">
                  Company Tagline 2
                </label>
                {/* <p className="">Title</p> */}
                <input
                  onChange={handleInputChange}
                  value={data.main_subtitle}
                  name="main_subtitle"
                  id="company_tagline_2"
                  type="text"
                  placeholder="Company Tagline 1"
                  className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                />
              </div>
            </div>
          </div>
        ) : (
          <ViewHeroBanner data={data} />
        )}
      </Collapsible>
    </form>
  );
}

function SectionOne({ data, setData, getDataByValueTab }) {
  const { activeTabs } = usePathViewContext();
  const { setIsOpenModalSuccess } = useSuccessModalContext();

  const [isEdit, setIsEdit] = React.useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = React.useState<boolean>(false);

  const handleAddMore = () => {
    setData((prev) => ({
      ...prev,
      section_1: {
        ...prev.section_1,
        flow_recruit: [...prev.section_1.flow_recruit, { flow: { body: '', title: '' } }],
      },
    }));
  };

  const handleDelete = (index: number) => {
    setData((prev) => ({
      ...prev,
      section_1: {
        ...prev.section_1,
        flow_recruit: prev.section_1.flow_recruit.filter((_, i) => i !== index),
      },
    }));
  };

  const handleInputChange = (e) => {
    const { name, files, value } = e.target;

    if (files && files.length > 0) {
      setData((prev) => ({
        ...prev,
        [name]: files[0],
      }));

      return;
    }

    setData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleInputRteChangeBody = (index?, name?, updatedContent?) => {
    setData((prev) => {
      const updatedFlowRecruit = [...prev.section_1.flow_recruit];

      updatedFlowRecruit[index] = {
        ...updatedFlowRecruit[index],
        flow: {
          ...updatedFlowRecruit[index].flow,
          body: updatedContent,
        },
      };

      return {
        ...prev,
        section_1: {
          ...prev.section_1,
          flow_recruit: updatedFlowRecruit,
        },
      };
    });
  };

  const handleInputRteChangeTitle = (e, index) => {
    const { name, value } = e.target; // Get the name and value from input event

    const newInputs = data.flow_recruit.map((input, i) =>
      i === index
        ? {
            ...input, // Copy all properties of the input
            flow: {
              ...input.flow,
              [name]: value, // Only change the title or other input
            },
          }
        : input
    );

    setData((prev) => ({
      ...prev,
      section_1: {
        ...prev.section_1,
        flow_recruit: newInputs,
      },
    }));
  };

  const handleDeleteFile = (name: string) => {
    setData((prev) => ({
      ...prev,
      section_1: {
        ...prev.section_1,
        [name]: '',
      },
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    setIsLoadingSubmit(true);
    const payload = {
      page_name: activeTabs.value,
      card_group: 'section_1', // '{faq/headers_footers/hero_banner/job_position/section_1/section_2/section_3}'
      data: data,
    };
    try {
      const response = await services.post('/setting/company_profile/save', payload);
      if (response.statusCode === 200) {
        getDataByValueTab();
        setIsEdit(false);
        setIsOpenModalSuccess(true);
      }
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoadingSubmit(false);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <Collapsible isEdit={isEdit} setIsEdit={setIsEdit} footer title="Section 1">
        {isEdit ? (
          <div className="p-3 space-y-5">
            <div className="space-y-3">
              {/* Title */}
              <div>
                <label htmlFor="title" className="w-full">
                  Title
                </label>
                {/* <p className="">Title</p> */}
                <input
                  onChange={handleInputChange}
                  value={data.tag_title_1}
                  name="tag_title_1"
                  id="title"
                  type="text"
                  placeholder="Title"
                  className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                />
              </div>

              {/* Subtitle */}
              <div>
                <label htmlFor="subtitle" className="w-full">
                  Subtitle
                </label>
                {/* <p className="">Title</p> */}
                <input
                  onChange={handleInputChange}
                  value={data.head_title_1}
                  name="head_title_1"
                  id="subtitle"
                  type="text"
                  placeholder="Subtitle"
                  className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                />
              </div>
            </div>

            {/* Description */}
            {data.flow_recruit.map((item, index) => {
              return (
                <div key={index}>
                  <div className="w-full flex items-center justify-start mb-1.5">
                    <p>Descriptions and Details</p>{' '}
                    <p className="text-red-500 text-xs">*Add at least 3 descriptions and details to be able to save</p>
                  </div>
                  <div className="flex items-center justify-between">
                    <p className="flex-1">{index + 1}. Description</p>
                    <button
                      onClick={() => handleDelete(index)}
                      type="button"
                      className="p-0.5 rounded-md hover:bg-gray-200 text-slate-600 disabled:bg-gray-100 disabled:cursor-not-allowed disabled:text-gray-400"
                    >
                      <LuTrash2 className="size-4" />
                    </button>
                  </div>
                  <input
                    value={item.flow.title}
                    onChange={(e) => handleInputRteChangeTitle(e, index)}
                    name="title"
                    id="description"
                    type="text"
                    placeholder="Add description"
                    className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                  />
                  <div className="mt-1.5">
                    <RichTextEditor index={index} name="body" contentHtml={item.flow.body} onChange={handleInputRteChangeBody} />
                  </div>
                </div>
              );
            })}

            <div className="flex items-center justify-end">
              <Button
                onClick={handleAddMore}
                type="button"
                variant="outlined"
                size="sm"
                className="normal-case text-xs text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center gap-x-1"
              >
                Add More
                <FaPlus className="size-3 text-blue-600" />
              </Button>
            </div>

            <div className="space-y-3">
              <div>
                <label htmlFor="section_image_src_1" className="flex items-center gap-1 mb-1.5">
                  Upload Image/Video
                  <Tooltip content="img ratio: 150 x 37.66">
                    <Button variant="text" size="lg" className="p-0">
                      <AiOutlineInfoCircle className="text-gray-500" />
                    </Button>
                  </Tooltip>
                  <span className="text-danger-500 text-xs">*Format image/video. max 1MB</span>
                </label>
                <InputFile
                  onRemove={() => handleDeleteFile('section_image_src_1')}
                  onChange={handleInputChange}
                  id="section_image_src_1"
                  name="section_image_src_1"
                  type="file"
                  value={data.section_image_src_1}
                />
              </div>
            </div>
          </div>
        ) : (
          <ViewSectionOne data={data} />
        )}
      </Collapsible>
    </form>
  );
}

function SectionTwo({ data, setData }) {
  const [isEdit, setIsEdit] = React.useState<boolean>(false);

  return (
    <Collapsible isEdit={isEdit} setIsEdit={setIsEdit} footer title="Section 2">
      {isEdit ? (
        <div className="p-3 space-y-5">
          <div className="space-y-3">
            {/* Title */}
            <div>
              <label htmlFor="title" className="w-full">
                Title
              </label>
              {/* <p className="">Title</p> */}
              <input
                id="title"
                type="text"
                placeholder="Title"
                className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
              />
            </div>

            {/* Subtitle */}
            <div>
              <label htmlFor="subtitle" className="w-full">
                Subtitle
              </label>
              {/* <p className="">Title</p> */}
              <input
                id="subtitle"
                type="text"
                placeholder="Subtitle"
                className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
              />
            </div>
          </div>

          {/* Description */}
          <div>
            <div className="w-full flex items-center justify-start mb-1.5">
              <p>Descriptions and Details</p> <p className="text-red-500 text-xs">*Add at least 4 descriptions and details to be able to save</p>
            </div>
            <label>1. Description</label>
            <input
              id="description"
              type="text"
              placeholder="Add description"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
            <div className="mt-1.5">
              <RichTextEditor contentHtml="" />
            </div>
          </div>

          <div className="flex items-center justify-end">
            <Button
              variant="outlined"
              size="sm"
              className="normal-case text-xs text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center gap-x-1"
            >
              Add More
              <FaPlus className="size-3 text-blue-600" />
            </Button>
          </div>
        </div>
      ) : (
        <ViewSectionTwo data={data} />
      )}
    </Collapsible>
  );
}

