import * as React from 'react';
import { IoEyeOutline, IoEyeOffOutline } from 'react-icons/io5';
import { LuTrash2 } from 'react-icons/lu';

type InputFileProps = React.InputHTMLAttributes<HTMLInputElement> & {
  value?: string;
  onRemove?: any;
  index?: number;
};

export default function InputFile({ value, onRemove, index, ...props }: InputFileProps) {
  const [isPreviewImage, setIsPreviewImage] = React.useState<boolean>(false);

  const handlePreview = () => {
    setIsPreviewImage(!isPreviewImage);
  };

  const handleDelete = () => {
    onRemove();
  };

  return (
    <div className="px-2 py-1.5 rounded-lg border border-dashed border-gray-400 flex items-center justify-between mt-1.5">
      <div className="flex items-center flex-1">
        {value ? (
          <p>{value}</p>
        ) : (
          <input
            {...props}
            className="file:appearance-none file:border-none file:text-sm file:bg-gray-100 file:text-slate-900 file:rounded-md file:py-1 file:font-medium file:hover:bg-gray-200 file:duration-150 file:active:scale-95 text-sm text-gray-400"
          />
        )}
      </div>

      {value && (
        <div className="flex items-center justify-end">
          <div className="border-r border-slate-400 flex items-center justify-center">
            <button onClick={handlePreview} type="button" className="p-0.5 rounded-md hover:bg-gray-200 mr-1">
              {isPreviewImage ? <IoEyeOffOutline className="size-5 text-slate-600" /> : <IoEyeOutline className="size-5 text-slate-600" />}
            </button>
          </div>

          <div className="flex items-center justify-center ml-1">
            <button
              onClick={handleDelete}
              type="button"
              className="p-0.5 rounded-md hover:bg-gray-200 text-slate-600 disabled:bg-gray-100 disabled:cursor-not-allowed disabled:text-gray-400"
            >
              <LuTrash2 className="size-4" />
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
