import * as React from 'react';
import Collapsible from '../../collapsible';
import { Switch, Button } from '@material-tailwind/react';
import { IoIosInformationCircleOutline } from 'react-icons/io';
import { IoEyeOutline, IoEyeOffOutline } from 'react-icons/io5';
import { LuTrash2 } from 'react-icons/lu';
import { FaPlus } from 'react-icons/fa6';
import DefaultImage from '../../../assets/svg/DefaultImage';

import RichTextEditor from '../../richtexteditor';

export default function AboutUs() {
  return (
    <main className="space-y-2">
      <Collapsible footer title="Section 1">
        <div className="p-4 text-sm space-y-4">
          {/* Title */}
          <div>
            <label htmlFor="title" className="w-full">
              Title
            </label>
            {/* <p className="">Title</p> */}
            <input
              id="title"
              type="text"
              placeholder="Welcome to the Humavue Platform"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
          </div>

          {/* Description */}
          <div>
            <label htmlFor="description" className="w-full">
              Description
            </label>
            <div className="mt-1.5">
              <RichTextEditor contentHtml="" />
            </div>
          </div>

          {/* Image 1*/}
          <div>
            <label htmlFor="upload_image_1" className="w-full flex items-center gap-x-1">
              <p>Upload Image 1</p>
              <IoIosInformationCircleOutline className="size-4 text-slate-500" />
              <p className="text-red-500 font-normal text-xs">*Format jpg/png/jpeg. max 1MB</p>
            </label>
            <div className="px-2 py-1.5 rounded-lg border border-dashed border-gray-400 flex items-center justify-between mt-1.5">
              <div className="flex items-center flex-1">
                <input
                  id="upload_image"
                  required
                  accept=".jpg, .jpeg, .png"
                  type="file"
                  name=""
                  className="file:appearance-none file:border-none file:text-sm file:bg-gray-100 file:text-slate-900 file:rounded-md file:py-1 file:font-medium file:hover:bg-gray-200 file:duration-150 file:active:scale-95 text-sm text-gray-400"
                />
              </div>
            </div>
          </div>

          {/* Image 2*/}
          <div>
            <label htmlFor="upload_image_2" className="w-full flex items-center gap-x-1">
              <p>Upload Image 2</p>
              <IoIosInformationCircleOutline className="size-4 text-slate-500" />
              <p className="text-red-500 font-normal text-xs">*Format jpg/png/jpeg. max 1MB</p>
            </label>
            <div className="px-2 py-1.5 rounded-lg border border-dashed border-gray-400 flex items-center justify-between mt-1.5">
              <div className="flex items-center flex-1">
                <input
                  id="upload_image"
                  required
                  accept=".jpg, .jpeg, .png"
                  type="file"
                  name=""
                  className="file:appearance-none file:border-none file:text-sm file:bg-gray-100 file:text-slate-900 file:rounded-md file:py-1 file:font-medium file:hover:bg-gray-200 file:duration-150 file:active:scale-95 text-sm text-gray-400"
                />
              </div>
            </div>
          </div>
        </div>
      </Collapsible>

      <Collapsible footer title="Section 2">
        <div className="p-4 text-sm space-y-4">
          {/* Title */}
          <div>
            <label htmlFor="title" className="w-full">
              Title
            </label>
            {/* <p className="">Title</p> */}
            <input
              id="title"
              type="text"
              placeholder="Welcome to the Humavue Platform"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
          </div>

          {/* Subtitle */}
          <div>
            <label htmlFor="subtitle" className="w-full">
              Subtitle
            </label>
            {/* <p className="">Title</p> */}
            <input
              id="subtitle"
              type="text"
              placeholder="Add Subtitle"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
          </div>

          {/* Description */}
          <div>
            <div className="w-full flex items-center justify-start mb-1.5">
              <p>Descriptions and Details</p> <p className="text-red-500 text-xs">*Add at least 3 descriptions and details to be able to save</p>
            </div>
            <label>1. Description</label>
            <input
              id="description"
              type="text"
              placeholder="Add description"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
            <div className="mt-1.5">
              <RichTextEditor contentHtml="" />
            </div>
          </div>

          <div className="flex items-center justify-end">
            <Button
              variant="outlined"
              size="sm"
              className="normal-case text-xs text-blue-600 border-blue-600 shadow shadow-blue-600 rounded-full flex items-center gap-x-1"
            >
              Add More
              <FaPlus className="size-3 text-blue-600" />
            </Button>
          </div>
        </div>
      </Collapsible>

      <Collapsible footer title="Section 3">
        <div className="p-4 text-sm space-y-4">
          {/* Title */}
          <div>
            <label htmlFor="title" className="w-full">
              Title
            </label>
            {/* <p className="">Title</p> */}
            <input
              id="title"
              type="text"
              placeholder="Welcome to the Humavue Platform"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
          </div>

          {/* Subtitle */}
          <div>
            <label htmlFor="subtitle" className="w-full">
              Subtitle
            </label>
            {/* <p className="">Title</p> */}
            <input
              id="subtitle"
              type="text"
              placeholder="Add Subtitle"
              className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
            />
          </div>

          {/* Description */}
          <div>
            <div className="w-full flex items-center justify-start mb-1.5">
              <p>Testimony</p> <p className="text-red-500 text-xs">*Add 3 testimony to be able to save</p>
            </div>
            <p>Testimony 1</p>
            <div className="flex items-start mt-2">
              <div className="w-[10%] flex items-center justify-center">
                <label className="cursor-pointer">
                  <div className="rounded-full border size-20 bg-gray-50 flex flex-col items-center justify-center ">
                    <DefaultImage />
                    <input accept=".jpg, .jpeg, .png" type="file" name="" className="hidden" />
                  </div>
                  <p className="text-center">Upload</p>
                </label>
              </div>

              <div className="flex-1">
                <div className="flex items-center space-x-2">
                  <div className="w-full">
                    <label htmlFor="name">Name</label>
                    <br />
                    <input
                      id="name"
                      type="text"
                      placeholder="Add name"
                      className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                    />
                  </div>
                  <div className="w-full">
                    <label htmlFor="position">Position</label>
                    <br />
                    <input
                      id="position"
                      type="text"
                      placeholder="Add position"
                      className="mt-1.5 text-sm px-4 py-2 rounded-md border w-full focus:border-blue-700 focus:outline-none"
                    />
                  </div>
                </div>
                <div className="mt-1.5">
                  <RichTextEditor contentHtml="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </Collapsible>
    </main>
  );
}
